import { dbService } from "fbase";
import React, { useState } from "react";

const GroupFactory = () => {
    const [making , setMaking] = useState(false);
    const [groupName, setGroupName] = useState("");
    const [groupPw, setGroupPw] = useState("");
    const [groupDe, setGroupDe] = useState("");
    const toggleMaking = () => setMaking((prev) => !prev);
    const onSubmit = async(event) => {
        event.preventDefault();
        if(groupName===""||groupPw===""||groupDe===""){
            return;
        }
        const groupObj = {
            groupName: groupName,
            groupPw: groupPw,
            groupDe: groupDe,
            createdAt: Date.now()
        }
        await dbService.collection("groups").add(groupObj);
        setGroupName("");
        setGroupPw("");
        setGroupDe("");
        toggleMaking();
    }
    const nameChange = (event) => {
        const {
            target: { value },
        } = event;
        setGroupName(value)
    }
    const pwChange = (event) => {
        const {
            target: { value },
        } = event;
        setGroupPw(value)
    }
    const deChange = (event) => {
        const {
            target: { value },
        } = event;
        setGroupDe(value)
    }
    return(
        <div className="groupForm_box">
            {making ? (
                <>
                <h4 style={{marginBottom:10,textAlign:"center"}}>Let's make a new group</h4>
                <form onSubmit={onSubmit}>
                <input onChange={nameChange} value={groupName} className="groupForm" placeholder="Group name"/><br/><br/>
                <input onChange={pwChange} value={groupPw} className="groupForm" placeholder="Password"/><br/><br/>
                <input onChange={deChange} value={groupDe}className="description" placeholder="Write a description"/><br/><br/><br/><br/>
                <input type="submit" value="Make" className="groupBtn" style={{backgroundColor:"#04aaff"}}/>
                <input type="submit" onClick={toggleMaking} value="Cancel" className="groupBtn" style={{backgroundColor:"tomato"}}/>
                </form>
                </>
            ) : (
                <>
                <input type="submit" onClick={toggleMaking} className="formBtn"  value="Make a new group"/>
                </>
            )}
        </div>
    )
}

export default GroupFactory;