const Groups = ({groupObj}) => {
    return (
        <>
            <a href={"/kwitter/room?id=" + groupObj.id} groupObj={groupObj}>
                <div className="group">
                    <h3>{groupObj.groupName}:</h3>
                    <h4>{groupObj.groupDe}</h4>
                </div>
            </a>
        </>
    )
}

export default Groups;