import GroupFactory from "components/GroupFactory";
import { dbService } from "fbase";
import React, { useEffect, useState } from "react";
import $ from 'jquery';
import Groups from "components/Groups";

const Group = () => {
    const [groups, setGroups] = useState([]);
    useEffect(() => {
        dbService.collection("groups").orderBy("createdAt","desc").onSnapshot((snapshot) => {
            const groupArray = snapshot.docs.map((doc) => ({
                id: doc.id,
                groupName: doc.get("groupName"),
                groupPw: doc.get("groupPw"),
                groupDe: doc.get("groupDe"),
                ...doc.data(),
            }));
            setGroups(groupArray);
        });
    }, []);
    return (
        <>
        <div className="groupForm_box">
            <GroupFactory />
                <div class="groupBox" style={{ marginTop: 30 }}>
                    {groups.map((group) => (
                        <Groups key={group.id} groupObj={group}/>
                            )
                        )
                    }
            </div>
        </div>
        </>
    )
}

export default Group;